body {
  position: relative;
  background-image: url('./paper.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

h1 {
  color: #00ea42;
  font-size: 40px;
  opacity: .85;
  font-family: 'Share Tech Mono', monospace;
}

button {
  border-radius: 10px;
  margin: 6px;
  -webkit-box-shadow: 2px 4px 2px 3px grey;
          box-shadow: 2px 4px 2px 3px grey;
  font-size: 40px;
  width: 75px;
  outline: none;
  font-family: 'Share Tech Mono', monospace;
}

button:active {
  background-color: grey;
  opacity: .95;
}

.calculator {
  position: absolute;
  width: 400px;
  height: 525px;
  border-style: double;
  top: 50%;
  left: 50%;
  margin: 100px 0 0 -200px;
  border-radius: 10px;
  background-color: #efeadc;
  -webkit-box-shadow: 2px 4px 2px 3px grey;
          box-shadow: 2px 4px 2px 3px grey;
}

.calc-window {
  height: 60px;
  width: 350px;
  border-style: solid;
  margin: 20px;
  background-color: #1c1d1e;
  border-radius: 4px;
}

.on-screen {
  float: right;
  margin: 0px;
  padding: 5px;
}

.buttons {
  margin: 0 auto;
}

.calc-row-main {
  margin: 20px;
}

.calc-row {
  margin: 20px;
}



